$Montserrat:  ("Montserrat", "Regular", 400),
              ("Montserrat","Bold", 700),
              ("fontawesome","light", 300);

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$main: #E5E5E5;
$grey: #E1E1E1;
$white: #FFFFFF;
$dark: #323232;
$black: #000000;
$light: #E1E1E1;
$darkGrey: #C4C4C4;
$newsBlock: linear-gradient(180deg, rgba(50, 50, 50, 0.75) 0%, rgba(50, 50, 50, 0.5) 100%);