@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #323232;
}

ul {
  list-style: none;
}

body {
  font-family: "Montserrat";
  background: #E5E5E5;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: "fontawesome";
}

.icon-search::before {
  content: "\f002";
  font-weight: 300;
  color: #323232;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header a {
  display: block;
}

.header__lang-menu {
  position: relative;
}

.header__lang-menu ul {
  margin: 0;
  padding: 0;
  background-color: #fff;
  border: 1px solid #f8f8f8;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  display: none;
  position: absolute;
  top: 30px;
  right: -15px;
}

.header__lang-menu ul li {
  display: flex;
  justify-content: space-between;
}

.header__lang-menu ul li a {
  width: 85px;
  display: block;
  padding: 5px 10px;
}

.header__lang-menu ul li a::before {
  content: '';
  display: inline-block;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-right: 5px;
  vertical-align: middle;
}

.header__lang-menu ul li a:hover {
  background-color: #f2f2f2;
}

.header__lang-menu:hover ul {
  display: block;
}

.header__selected-lang {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  line-height: 2;
  width: 60px;
}

.header__selected-lang::before {
  content: '';
  display: block;
  width: 30px;
  height: 25px;
  background-image: url(https://www.countryflags.io/ru/flat/32.png);
}

.header__uz::before {
  background-image: url(https://www.countryflags.io/uz/flat/32.png);
}

.header__ru::before {
  background-image: url(https://www.countryflags.io/ru/flat/32.png);
}

.header__en::before {
  background-image: url(https://www.countryflags.io/us/flat/32.png);
}

.header__lang {
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
}

.header__logo {
  font-weight: 700;
  font-size: 32px;
  line-height: 23px;
  padding: 18px 0;
}

.header__checkbox {
  display: none;
}

.header__box {
  margin-top: 50px;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  transition: .5s;
}

.header__box label {
  position: absolute;
  right: 0;
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 23px;
  line-height: 60px;
  border-radius: 50%;
  cursor: pointer;
  transition: .5s;
  box-shadow: -5px -5px 8p #edeff1, 5px 5px 10px #cacfd8;
}

.header__box label:hover {
  box-shadow: -10px -10px 15px #edeff1, 10px 10px 15px #cacfd8;
  transform: scale(1.1);
}

.header__box input {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
  border-radius: 30px;
  outline: none;
  font-size: 19px;
  padding-left: 20px;
  box-shadow: -10px -10px 15px #edeff1, 10px 10px 15px #cacfd8;
  transition: .5s;
  opacity: 0;
  color: #323232;
}

.header__box input:focus {
  box-shadow: -10px -10px 40px #edeff1, 10px 10px 40px #cacfd8;
}

#check:checked + .header__box {
  width: 280px;
}

#check:checked + .header__box > input {
  opacity: 1;
}

#check:checked + .header__box > label {
  box-shadow: none;
}

#check:checked + .header__box > label:hover {
  transform: scale(1);
}
