.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
        display: block;
    }

    &__lang-menu {
        position: relative;
        ul {
            margin: 0;
            padding: 0;
            background-color: #fff;
            border: 1px solid #f8f8f8;
            box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
            border-radius: 5px;
            display: none;
            position: absolute;
            top: 30px;
            right: -15px;

            li {
                display: flex;
                justify-content: space-between;
                a {
                    width: 85px;
                    display: block;
                    padding: 5px 10px;
                    
                    &::before {
                        content: '';
                        display: inline-block;
                        align-items: center;
                        width: 32px;
                        height: 32px;
                        margin-right: 5px;
                        vertical-align: middle;
                    }

                    &:hover {
                        background-color: #f2f2f2;
                    }
                }
            }
        }

        &:hover {
            ul {
                display: block;
            }
        }
    }

    &__selected-lang {
        cursor: pointer;
        display: flex;
        justify-content: space-between;
        line-height: 2;
        width: 60px;

        &::before {
            content: '';
            display: block;
            width: 30px;
            height: 25px;
            background-image: url(https://www.countryflags.io/ru/flat/32.png);
        }
    }

    &__uz::before {
        background-image: url(https://www.countryflags.io/uz/flat/32.png);
    }
    &__ru::before {
        background-image: url(https://www.countryflags.io/ru/flat/32.png);
    }
    &__en::before {
        background-image: url(https://www.countryflags.io/us/flat/32.png);
    }

    &__lang {
        font-weight: 400;
        font-size: 18px;
        line-height: 13px;
    }

    &__logo {
        font-weight: 700;
        font-size: 32px;
        line-height: 23px;
        padding: 18px 0;
    }

    &__checkbox {
        display: none;
    }

    &__box {
        margin-top: 50px;
        transform: translate(-50%, -50%);
        width: 60px;
        height: 60px;
        transition: .5s;
        label {
            position: absolute;
            right: 0;
            width: 60px;
            height: 60px;
            text-align: center;
            font-size: 23px;
            line-height: 60px;
            border-radius: 50%;
            cursor: pointer;
            transition: .5s;
            box-shadow: -5px -5px 8p #edeff1,
            5px 5px 10px #cacfd8;

            &:hover {
                box-shadow: -10px -10px 15px #edeff1,
                10px 10px 15px #cacfd8;
                transform: scale(1.1);
            }
        }

        input {
            width: 100%;
            height: 100%;
            background-color: transparent;
            border: none;
            border-radius: 30px;
            outline: none;
            font-size: 19px;
            padding-left: 20px;
            box-shadow: -10px -10px 15px #edeff1,
            10px 10px 15px #cacfd8;
            transition: .5s;
            opacity: 0;
            color: $dark;

            &:focus {
                box-shadow: -10px -10px 40px #edeff1,
                10px 10px 40px #cacfd8;
            }
        }
    
    }

}

#check:checked+.header__box {
    width: 280px;
}
#check:checked+.header__box > input {
    opacity: 1;
}
#check:checked+.header__box > label {
    box-shadow: none;
}
#check:checked+.header__box > label:hover {
    transform: scale(1);
}