* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: $dark;
}

ul {
    list-style: none;
}

body {
    font-family: "Montserrat";
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: "fontawesome";
}

.icon {
    &-search::before {
        content: "\f002";
        font-weight: 300;
        color: $dark;
    }
}

